  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    display: flex;
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1920px;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  margin: 0 auto; }

.header {
  position: fixed;
  top: 0;
  z-index: 10;
  display: block;
  width: 100%;
  padding: 25px 35px;
  background-color: #ffffff; }
  .header svg {
    pointer-events: none; }

.preloader {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1100;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  height: calc(100 * var(--vh, 1vh));
  padding: 110px;
  background-color: #ffffff;
  opacity: 1;
  transition: opacity 0.6s ease; }
  .preloader svg {
    width: 100%;
    transform: translateX(-100vw);
    opacity: 0;
    transition: transform 0.6s ease; }
  .preloader p {
    align-self: flex-end;
    margin: 0;
    font-size: 24px;
    line-height: 22px; }
  @media (max-width: 1023px) {
    .preloader p:first-of-type {
      display: none; } }
  .preloader p:last-of-type {
    display: none; }
    @media (max-width: 1023px) {
      .preloader p:last-of-type {
        display: unset; } }
  .preloader:not(.js-preloader) {
    display: none; }
  .preloader.is-loading svg {
    transform: translateX(0);
    opacity: 1; }
  .preloader.is-hidden {
    opacity: 0; }
    .preloader.is-hidden svg {
      transform: translateX(-100vw); }

.intro {
  position: relative;
  display: grid;
  align-items: center;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  padding: 82px 35px;
  transform: translateX(-100%);
  opacity: 0;
  transition: opacity 0.6s ease, transform 0.6s ease;
  grid-column-gap: 40px;
  grid-template-columns: 58.9% auto; }
  @media (max-width: 1023px) {
    .intro {
      padding: 0;
      grid-template-columns: unset; }
      .intro.js-intro {
        grid-template-areas: "intro"; }
        .intro.js-intro .intro__wrapper {
          transform: translateX(0%);
          opacity: 1;
          transition: opacity 0.6s ease, transform 0.6s ease;
          grid-area: intro; }
        .intro.js-intro .intro__wrapper--blue {
          transform: translateX(-100%);
          visibility: visible;
          opacity: 0; }
        .intro.js-intro .intro__wrapper--show {
          transform: translateX(0%);
          visibility: visible;
          opacity: 1; } }
  .intro--show {
    transform: translateX(0);
    opacity: 1; }
  .intro h1 {
    margin: 0;
    font-weight: 400;
    font-size: 96px;
    line-height: 96px;
    color: #2c39f2; }
  .intro p {
    max-width: 352px;
    margin: 0;
    margin-top: 30px;
    margin-bottom: 75px;
    font-size: 30px;
    line-height: 40px; }
    @media (max-width: 1023px) {
      .intro p {
        margin-bottom: 100px; } }
  .intro .intro__wrapper {
    z-index: 1;
    background-color: transparent; }
    @media (max-width: 1023px) {
      .intro .intro__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 100vh;
        min-height: calc(100 * var(--vh, 1vh));
        padding: 80px 35px;
        background-color: #ffffff; }
        .intro .intro__wrapper--blue {
          background-color: #2c39f2; }
          .intro .intro__wrapper--blue p {
            color: #ffffff; }
          .intro .intro__wrapper--blue .white {
            fill: #ffffff; } }
  .intro .intro__bg {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 0;
    display: flex;
    justify-content: center;
    align-items: flex-end; }
    @media (max-width: 1023px) {
      .intro .intro__bg {
        display: none; } }

